// Class helpers

.fade {
}

.fadeOut {
  opacity: 0;
}
.fadeIn {
  animation: fade-in 0.7s ease-in forwards;
}

//  ---------------------------------------------------//
//  -----------------   Animation
//  ---------------------------------------------------//

@keyframes herocolourfade {
  from {
    background-image: radial-gradient(
      circle,
      transparent,
      transparentize($pink-light, 0.1) 100%
    );
  }
  to {
    background-image: radial-gradient(
      circle,
      transparent,
      transparentize($pink-light, 0.1) 100%
    );
  }
}

@keyframes bg {
  from {
    background-position: left;
  }
  to {
    background-position: right;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

